export function setKey (key) {
  var _key = JSON.stringify(key)
  localStorage.setItem('userinfo', _key)
  return _key
}

export function removeToken () {
  localStorage.removeItem('userinfo')
}

export function getToken () {
  return JSON.parse(localStorage.userinfo)['access-token']
}

export function getExpires () {
  return JSON.parse(localStorage.userinfo).expires_in
}

export function getSalesman () {
  return JSON.parse(localStorage.userinfo).salesman
}

export function getKey () {
  return JSON.parse(localStorage.userinfo).key
}

export function getUid () {
  return JSON.parse(localStorage.userinfo).uid
}
