<template>
  <div class="jun-input-box">
    <svg-icon :icon-class='iconClass' v-if="iconClass"></svg-icon>
    <input
      :type="type"
      :placeholder="placeholder"
      v-model="NewValue"
      @focus="focus"
      @blur="blur"
      :style="{color}"
      :disabled="disabled"/>
    <svg-icon class="clear" :style="{color}" @click="clearData" icon-class='clear' v-if='clearShow && clear'></svg-icon>
  </div>
</template>

<script>
// @ is an alias to /src
import SvgIcon from '@/components/SvgIcon'

export default {
  name: 'jun-input',
  data () {
    return {
      clearShow: false
    }
  },
  components: {
    SvgIcon
  },
  props: {
    value: {},
    color: {
      default: '#333333'
    },
    type: {
      type: String,
      // required: true
      default: 'text'
    },
    clear: [Boolean, String],
    iconClass: {
      type: String
    },
    className: {
      type: String
    },
    placeholder: {
      type: String
    },
    disabled: {
      type: Boolean,
      default: false
    }
  },
  computed: {
    NewValue: {
      get () {
        return this.value
      },
      set (value) {
        this.$emit('input', value)
      }
    }
  },
  // watch: {
  //   NewValue (val) {
  //     if (val) {
  //       this.clearShow = true
  //     } else {
  //       this.clearShow = false
  //     }
  //   }
  // },
  methods: {
    clearData () {
      this.NewValue = ''
    },
    blur () {
      this.clearShow = false
    },
    focus () {
      this.clearShow = true
    }
  }
}
</script>

<style scoped lang='scss'>
  @import '@/style/mixin.scss';
  .jun-input-box {
    @include wh(100%, 100%);
    @include fb(space-between);
    position: relative;
    svg:first-of-type {
      @include space(padding-left, 0.1rem);
    }
    .clear {
      @include wh(0.36rem, 0.36rem);
      // color:#e4e4e4;
      position: absolute;
      top: 50%;
      right: 0.3em;
      transform: translate(0, -50%);
      &:active {
        opacity: 0.5;
      }
    }
    input {
      @include space(padding-left, 0.5em);
      // color: #e4e4e4;
      background: none;
      flex: 1;
      min-height: 0.6rem;
    }
  }
</style>
