Vue.use(VueLoading)
Vue.use(VueToast)
Vue.use(VueMessage)
Vue.use(VueQriously)
Vue.use(waves)
Vue.prototype.request = request
Vue.prototype.$baseUrl = process.env.VUE_APP_BASE_URL
// 阻止 vue 在启动时生成生产提示
