import Vue from 'vue'
import axios from 'axios'
import router from '../router'
import crypto from './aes.js'
import * as auth from './auth'

const _Vue = new Vue()
const serverKey = 'H4CeTVcg1zUFwTYK'
const service = axios.create({
  baseURL: process.env.VUE_APP_BASE_API, // api的base_url
  timeout: 6000, // request timeout
  headers: { 'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8', 'Content-Encrypt': '2' }
  // headers: { 'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8', 'debug': '1' }
})

// request interceptor 添加请求拦截器
service.interceptors.request.use(config => {
  var judge = config.url.indexOf('auth') === -1 && config.url.indexOf('ver-sms') === -1 && (config.url.indexOf('user') === -1 || config.method !== 'post') && config.url.indexOf('rest-password') === -1
  if (judge) {
    if (config.method !== 'delete' && config.method !== 'get' && (config.data !== undefined || config.data !== {})) {
      const encode = crypto.Encrypt(config.data, auth.getKey())
      config.data = JSON.stringify({ data: encode })
    }
  } else {
    const encode = crypto.Encrypt(config.data, serverKey)
    config.data = JSON.stringify({ data: encode })
  }
  if (config.loading) {
    _Vue.$loading()
  }
  if (localStorage.userinfo) {
    const token = auth.getToken()
    config.headers['access-token'] = token // 让每个请求携带token-- ['X-Token']为自定义key 请根据实际情况自行修改
  }
  return config
}, error => {
  // Do something with request error
  console.log(error) // for debug
  Promise.reject(error)
})

// respone interceptor 添加响应拦截器
service.interceptors.response.use(
  response => {
    const res = response.data
    if (res.code === 401) {
      auth.removeToken()
      router.push('/')
    } if (res.code === 101) {
      auth.removeToken()
    } else if (res.code === 0) {
    } else {
      if (response.data.data) {
        const decode = crypto.Decrypt(response.data.data, serverKey)
        response.data.data = decode
      }
      response.data.headers = response.headers
    }
    _Vue.$loading.end()
    return response.data
  },
  error => {
    _Vue.$loading.end()
    // 断网或者请求超时状态
    if (error.message.includes('timeout')) {
      _Vue.$toast('请求超时，请检查网络是否连接正常')
    } else {
      _Vue.$toast(error.message)
    }
    return Promise.reject(error)
  }
)

export default service
