<template>
  <transition :name="animateName">
    <div class="confirm" v-show="isShow">
      <div class="confirm-box">
        <h2>{{ title }}</h2>
        <p>{{ message }}</p>
        <div class="btn">
          <button @click="cancel" v-show="cancelButtonShow">取消</button>
          <button @click="confirm">确定</button>
        </div>
      </div>
    </div>
  </transition>
</template>
<script type="text/babel">
export default {
  name: 'confirm',
  data () {
    return {
      hasAnimate: true
    }
  },
  props: {
    isShow: {
      type: Boolean,
      default: false
    },
    title: {
      type: String,
      default: '提示'
    },
    message: {
      type: String,
      default: '请输入标题内容...'
    },
    cancelButtonShow: {
      type: Boolean,
      default: true
    }
  },
  computed: {
    /**
     * 动画效果样式，没有返回空
     * @return {String} 样式
     */
    animateName () {
      return this.hasAnimate ? 'opacity' : ''
    }
  },
  methods: {
    cancel () {
      this.$emit('cancel')
    },
    confirm () {
      this.$emit('confirm')
    },
    show () {
      this.isShow = true
    },
    /**
     * 隐藏动画confirm
     */
    hide () {
      setTimeout(() => {
        this.isShow = false
      }, 333)
    }
  }
}
</script>

<style lang="scss" scoped>
  @import '@/style/mixin.scss';
  @keyframes confirm {
    0% { transform: rotate(0deg) }
    100% { transform: rotate(360deg) }
  }
  .opacity-leave-active,
  .opacity-enter-active {
    transition: all .5s;
  }
  .opacity-enter {
    opacity: 0;
    transform: translateZ(-30px);
  }
  .opacity-leave-to {
    opacity: 0;
    transform: translateZ(30px);
  }
  .confirm {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.6);
    .confirm-box {
      @include center;
      @include wh(60%, 3rem);
      @include br(0.1rem);
      @include fb(ver-center);
      background: $white;
      h2 {
        @include space(padding, 0.3rem);
        @include sc(.36rem, #303133)
      }
      p {
        flex: 1;
        @include sc(0.28rem, #606266);
      }
      .btn {
        @include wh(100%, 1rem);
        @include fb(space-around);
        @include space(padding, 0 0.6rem);
        border-top: 1px solid #cccccc;
        button {
          @include space(padding, 0.13rem 0.26rem);
          @include br(0.4rem);
          @include sc(0.25rem, #606266);
          background: none;
          border: 1px solid #dcdfe6;
        }
        button:first-child:active {
          color: #409EFF;
          border-color: #409EFF;
        }
        button:last-child {
          background: #409EFF;
          color: $white;
          border: none;
        }
      }
    }
  }
</style>
