<template>
  <div class="dialog" @click="hide" v-show="show">
    <slot></slot>
  </div>
</template>

<script>
// @ is an alias to /src

export default {
  name: 'jdialog',
  components: {
  },
  model: {
    prop: 'show',
    event: 'change'
  },
  props: {
    show: {
      type: Boolean,
      default: false
    }
  },
  computed: {
  },
  watch: {
    show (val) {
      this.$emit('update:show', val)
      this.$emit(val ? 'on-show' : 'on-hide')
    }
  },
  data () {
    return {
    }
  },
  created () {
  },
  methods: {
    hide (e) {
      // if (this.hideOnBlur) {
      //   this.$emit('update:show', false)
      //   this.$emit('change', false)
      //   this.$emit('on-click-mask')
      // }
      if (e.target === e.currentTarget) {
        this.$emit('update:show', false)
        this.$emit('change', false)
      }
    }
  }
}
</script>

<style scoped lang="scss">
  @import '@/style/mixin.scss';
  .dialog {
    @include wh(100%);
    @include fb(center-column);
    position: fixed;
    top: 0;
    left: 0;
    background: rgba(0, 0, 0, 0.6);
    z-index: 2000;
    animation:identifier .8s;
  }
  @keyframes identifier {
    0% {
      opacity: 0;
    }
    100% {
      opacity: 1;
    }
  }
</style>
