import request from '@/utils/request'

export function login (data) {
  return request({
    url: '/auth',
    method: 'post',
    data
  })
}

export function userInfo (uid) {
  return request({
    url: `/user/${uid}`,
    method: 'get'
  })
}

export function logOut (uid) {
  return request({
    url: `/auth/${uid}`,
    method: 'delete'
  })
}
