import Confirm from './index.vue'

export default {
  /**
   * 每个插件都有的install方法，用于安装插件
   * @param {Object} Vue - Vue类
   * @param {Object} [pluginOptions] - 插件安装配置
   */
  install (Vue, pluginOptions = {}) {
    // 创建"子类"方便挂载
    const VueConfirm = Vue.extend(Confirm)
    let $vm = null

    /**
     * 初始化并显示message
     * @returns {Promise} Promise实例
     */
    function $confirm (options) {
      return new Promise((resolve, reject) => {
        function mount () {
          $vm = new VueConfirm()
          // 手动创建一个未挂载的实例
          $vm.$mount()
          // 挂载
          document.querySelector(pluginOptions.container || 'body').appendChild($vm.$el)
        }
        // 第一次调用
        if (!$vm) {
          mount()
        }
        if (typeof options === 'object') {
          for (const key in options) {
            $vm[key] = options[key]
          }
        } else {
          mount()
          $vm.message = options
        }
        $vm.$off('cancel')
        $vm.$off('confirm')
        // 显示message
        $vm.show()
        $vm.$on('cancel', res => {
          $vm.hide()
          reject(options)
        })
        $vm.$on('confirm', res => {
          $vm.hide()
          resolve(options)
        })
      })
    }
    Vue.message = Vue.prototype.$confirm = $confirm
  }
}
