import { login, userInfo, logOut } from '@/api/user'
import store from '@/router'
import * as auth from '@/utils/auth'
// import { UpdateUserInfo } from '@/utils/updateToken' // expires updateToken

const user = {
  state: {
    userInfo: {},
    baseUrl: process.env.VUE_APP_BASE_URL
  },

  mutations: {
    SET_USER_INFO: (state, userInfo) => {
      state.userInfo = userInfo
    }
  },

  actions: {
    // 用户名登录,获取公钥
    async login ({ dispatch, commit, state }, userInfo) {
      const res = await login(userInfo)
      if (res.code !== 1) return Promise.reject(res)
      else {
        auth.setKey(res.data)
        window.location.reload()
      }
    },

    // 获取用户信息
    async GetUserInfo ({ commit, state }) {
      const result = await userInfo(auth.getUid())
      commit('SET_USER_INFO', result.data)
      return Promise.resolve(result)
    },

    // 退出
    async LogOut ({ commit, state }) {
      await logOut(auth.getUid())
      auth.removeToken()
      store.push({ name: 'login' })
    }
  }
}

export default user
