<template>
  <transition :name="animateName">
    <div class="loading" v-show="isShow">
      <div class="loading-box">
        <svg-icon icon-class="loading"></svg-icon>
        <p>Loading</p>
      </div>
    </div>
  </transition>
</template>
<script type="text/babel">
export default {
  data () {
    return {
      isShow: false,
      hasAnimate: true
    }
  },
  computed: {
    /**
     * 动画效果样式，没有返回空
     * @return {String} 样式
     */
    animateName () {
      return this.hasAnimate ? 'opacity' : ''
    }
  },
  methods: {
    /**
     * 开启动画效果
     */
    opemAnimate () {
      this.hasAnimate = true
    },
    /**
     * 去除动画效果
     * @return {Promise} 返回promise
     */
    removeAnimate () {
      return new Promise((resolve) => {
        this.hasAnimate = false
        resolve()
      })
    },
    /**
     * 显示动画loading
     */
    show () {
      this.isShow = true
    },
    /**
     * 隐藏动画loading
     */
    hide () {
      setTimeout(() => {
        this.isShow = false
      }, 666)
    }
  }
}
</script>

<style lang="scss" scoped>
  @import '@/style/mixin.scss';
  @keyframes loading {
    0% { transform: rotate(0deg) }
    100% { transform: rotate(360deg) }
  }
  .opacity-leave-active,
  .opacity-enter-active {
    transition: all .5s;
  }
  .opacity-enter {
    opacity: 0;
    transform: translateZ(-30px);
  }
  .opacity-leave-to {
    opacity: 0;
    transform: translateZ(30px);
  }
  .loading {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    .loading-box {
      @include center;
      @include wh(2.4rem, 2.2rem);
      @include br(0.1rem);
      @include fb(center-column);
      background: rgba(0, 0, 0, 0.6);
      svg {
        animation: loading 1s steps(16, end) infinite;
        color: $white;
      }
      p {
        @include space(padding-top, .3rem);
        @include sc(.3rem, $white);
      }
    }
  }
</style>
