<template>
  <div class="login">
    <div class="container">
      <img src="@/assets/login/logo.png" alt="">
      <ul class="main">
        <li>
          <jun-input v-model="form.phone"
            color="#e4e4e4"
            icon-class="phone"
            placeholder="请输入手机号码"
            clear='true'>
          </jun-input>
        </li>
        <li>
          <jun-input v-model="form.password"
            color="#e4e4e4"
            type="password"
            icon-class="password"
            placeholder="请输入密码"
            clear='true'>
          </jun-input>
        </li>
      </ul>
      <button @click="login">登 录</button>
      <button @click="$router.push('/register')">注 册</button>
      <router-link to="resetPassword" tag="p">忘记密码？</router-link>
    </div>
    <JDialog v-model="show"/>
  </div>
</template>

<script>
// @ is an alias to /src
import JunInput from '@/components/JunInput'
import JDialog from '@/components/JDialog'

export default {
  name: 'login',
  components: {
    JunInput,
    JDialog
  },
  data () {
    return {
      show: false,
      form: {
        phone: '',
        password: ''
      }
    }
  },
  created () {
  },
  methods: {
    login () {
      this.$store.dispatch('login', this.form).catch(error => {
        this.$toast(error.message)
      })
    }
  },
  mounted () {
  }
}
</script>

