<template>
  <transition :name="animateName">
    <div class="toast" v-show="isShow" :class="toastClass">
      <p>{{ message ? message : '成功啦！！' }}</p>
    </div>
  </transition>
</template>
<script type="text/babel">
export default {
  data () {
    return {
      isShow: false,
      hasAnimate: true,
      timer: ''
    }
  },
  props: {
    message: [String],
    position: [String]
  },
  computed: {
    /**
     * 动画效果样式，没有返回空
     * @return {String} 样式
     */
    animateName () {
      return this.hasAnimate ? 'jun-animate' : ''
    },
    toastClass () {
      return {
        // 'j-toast_forbidden': this.type === 'warn',
        // 'j-toast_cancel': this.type === 'cancel',
        // 'j-toast_success': this.type === 'success',
        // 'j-toast_text': this.type === 'text',
        'j-toast-top': this.position === 'top',
        'j-toast-bottom': this.position === 'bottom',
        'j-toast-middle': this.position === 'middle'
      }
    }
  },
  mounted () {
  },
  methods: {
    /**
     * 开启动画效果
     */
    opemAnimate () {
      this.hasAnimate = true
    },
    /**
     * 去除动画效果
     * @return {Promise} 返回promise
     */
    removeAnimate () {
      return new Promise((resolve) => {
        this.hasAnimate = false
        resolve()
      })
    },
    /**
     * 显示动画loading
     */
    show () {
      this.isShow = true
      clearInterval(this.timer)
      this.timer = setInterval(() => {
        this.isShow = false
      }, 2000)
    },
    /**
     * 隐藏动画loading
     */
    hide () {
      this.isShow = false
    }
  }
}
</script>

<style lang="scss" scope>
  @import '@/style/mixin.scss';
  .toast {
    @include br(.1rem);
    @include fb(center-column);
    z-index: 10000;
    min-width: 3rem;
    min-height: 1.2rem;
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background: rgba(0, 0, 0, 0.6);
    p {
      color: $white;
      @include space(padding, 0 .2rem)
    }
  }
  .j-toast-top {
    top: 2%;
    left: 50%;
    transform: translateX(-50%);
    transition: opacity 0.3s, transform .4s;
  }
  .jun-animate-enter-active, .jun-animate-leave-active {
    transition: all .5s;
  }
  .jun-animate-enter, .jun-animate-leave-active {
    opacity: 0;
  }
</style>
